<template>
  <div id='LifeTimeline'>
    <div id='LifeBeforeBirth'>
      Date of birth :
      <input type="date" v-model='birthDate'
        :min='age'
      >
      <div>
        <a target="_blank" href="http://perspective.usherbrooke.ca/bilan/servlet/BMTendanceStatPays?codeTheme=3&codeStat=SP.DYN.LE00.IN&codePays=FRA&optionsPeriodes=Aucune&codeTheme2=3&codeStat2=SP.DYN.LE00.IN&codePays2=POL&optionsDetPeriodes=avecNomP">
        Life expectancy at birth :
        </a>
        <input type="number" v-model='lifeExpectancyAtBirth'>
      </div>

      <div style='position:absolute; bottom:0; '>
          Born {{moment(birthDate).fromNow()}}
      </div>
    </div>
    <div id='LifeBeforeDeath'>
      <div id='Lived' :style="{height: livedPercentOfYears + '%'}">
        <div id='percentLived'>
         {{livedPercentOfYears + ' %'}} gone
        </div>
      </div>
      <div id='Remaining' :style="{height: 100-livedPercentOfYears + '%'}">
        {{100 - livedPercentOfYears + ' %'}} left
      </div>
    </div>
    <div id='LifeAfterDeath'>
      Expected to leave {{ moment(birthDate).add(lifeExpectancyAtBirth, 'year').fromNow() }}
    </div>
  </div>
</template>

<script>

import moment from 'moment'

export default {
  name: 'LifeTimeline',
  data () {
    return {
      birthDate: '1994-09-24',
      lifeExpectancyAtBirth: 77.65,
      sex: 'Male',
      moment: moment
    }
  },
  computed: {
    age: function () {
      return this.moment().diff(this.moment(this.birthDate), 'years')
    },
    livedPercentOfYears: function () {
      return Math.round(this.age / this.lifeExpectancyAtBirth * 100)
    }
  }
}
</script>

